@import "../../sass/variables";

.language-toggle-container {
  margin-top: 20px;
  font-size: 20px;

  .language-toggle-label {
    font-size: 18px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    .collapsed-toggle {
      width: 35px;
      height: 35px;

      &:hover {
        cursor: pointer;
        border-radius: 50%;
        background: $bqcHoverColor;
      }
    }
  }

  .language-items {
    .checkbox-item {
      height: 50px;
      border-radius: 5px;
      margin-top: 10px;
      background: rgba(171, 172, 192, 0.2);
      padding: 5px;
      line-height: 40px;

      & input[type="radio"] {
        opacity: 0;

        & + label {
          display: inline-block;
          position: relative;
          padding-left: 50px;
          cursor: pointer;

          &:before {
            content: "";
            position: absolute;
            left: -10px;
            top: 0;
            z-index: 1;
            width: 45px;
            height: 35px;
            border-radius: 7px;
            border: 1px solid #dddddd;
            background: rgba(171, 172, 192, 0.5);
          }
        }
      }

      & input[type="radio"]:checked {
        & + label {
          &:before {
            background: var(--accent-background);
          }

          &:after {
            content: "";
            position: absolute;
            z-index: 1;
            transition: all 0.2s ease;
            left: 7.5px;
            top: 10px;
            width: 10px;
            height: 5px;
            border-radius: 1px;
            border-left: 4px solid white;
            border-bottom: 4px solid white;
            transform: rotate(-45deg);
          }
        }
      }
    }
  }
}
